const gallery = require('./js/01-gallery').default;
const video = require('./js/02-video').default;
const feedback = require('./js/03-feedback').default;



switch (checkEndWord(location.pathname)) {
  case '01-gallery':
    gallery();
    break;
  case '02-video':
    video();
    break;
  case '03-feedback':
    feedback();
    break;
}

function checkEndWord(str) {
  let newPath = '';
  let newPathRev = '';
  let check = false;
  for (let i = str.length - 1; i > 0; i--) {
    if (str[i] === '/') break;
    if (check) newPath += str[i];
    if (str[i] === '.') check = true;
  }

  for (let i = newPath.length - 1; i >= 0; i--) {
    newPathRev += newPath[i];
  }

  return newPathRev;
}
